* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-info {
  font-size: 28px;
}

.country-list {
  list-style: none;
  padding-left: 0;
}

.country-name {
  font-size: 46px;
  margin: 0;
}

.item .country-name {
  font-size: 30px;
}

.bold {
  font-weight: 700;
}
